import React, { useState } from "react";
import "./About.css";
import "./AboutMedia.css";

import { AppContext } from "../../contexts/AppContext";

function About() {
  // contexts
  const aboutItems = React.useContext(AppContext).about;

  const [isAnimActive, setIsAnimActive] = useState(false);

  React.useEffect(() => {
    let heightAAbout =
      document.querySelector(".about").getBoundingClientRect().top + window.pageYOffset;

    function handleWindowScroll() {
      if (window.pageYOffset > heightAAbout - 300) {
        setIsAnimActive(true);
      }
    }
    document.addEventListener("scroll", handleWindowScroll);
    return () => {
      document.addEventListener("scroll", handleWindowScroll);
    };
  }, []);
  // =================================================================================

  return (
    <section className="about">
      <div className="about__container container">
        <h2 className="about__title text__title">{aboutItems.title}</h2>
        <p className="about__text text__text">
          {aboutItems.text}
          {/* Ассортимент сайдинга &laquo;Альта-Профиль&raquo; уникален для российского рынка: 8
          коллекций, в каждой - от 5 до 15 цветовых решений. Ассортимент сайдинга
          &laquo;Альта-Профиль&raquo; уникален для российского рынка: 8 коллекций, в каждой - от 5
          до 15 цветовых решений. */}
        </p>
        <div className="about__bg-image"></div>
        <div className="about__links">
          <div className="about__link-item">
            <a href="#" className="about__link ">
              {aboutItems.readMore} <span className="arrow arrow_right"></span>
            </a>
          </div>
          <div className="about__link-item">
            <a href="#" className="about__link ">
              {aboutItems.contacts} <span className="arrow arrow_right"></span>
            </a>
          </div>
        </div>
        <ul className="about__items">
          <li className="about__item">
            <h3 className="about__item-title">
              <span
                className={
                  isAnimActive ? `about__anim-item-n-1 _active-n-1` : `about__anim-item-n-1`
                }
              ></span>
            </h3>
            <p className="about__item-text">{aboutItems.materials}</p>
          </li>
          <li className="about__item">
            <h3 className="about__item-title">
              <span
                className={
                  isAnimActive ? `about__anim-item-n-2 _active-n-2` : `about__anim-item-n-2`
                }
              ></span>{" "}
              <span className="about__item-title_small">{aboutItems.year}</span>
            </h3>
            <p className="about__item-text">{aboutItems.serviceLife}</p>
          </li>
          <li className="about__item">
            <h3 className="about__item-title">
              <span
                className={
                  isAnimActive ? `about__anim-item-n-3 _active-n-3` : `about__anim-item-n-3`
                }
              ></span>
            </h3>
            <p className="about__item-text">
              {aboutItems.year} <br /> {aboutItems.company}
            </p>
          </li>
        </ul>
        <a href="#" className="about__button button">
          {aboutItems.button}
        </a>
      </div>
    </section>
  );
}

export default About;
