import React, { useState } from "react";

function HeaderLeftMenuItem(props) {
  function handleLeftMenuItemClick(e) {
    props.onClick(e.target.closest(".header__left-item").id);
  }

  // скрыть картинки в левом меню ======================
  const [styleImg, setstyleImg] = useState({
    background: `url(${props.img}) center/contain no-repeat`,
  });

  React.useEffect(() => {
    let heightItem = document.querySelector(".header__left-item").clientHeight;
    if (heightItem < 60) {
      setstyleImg({ display: "none" });
      console.log("min");
    }
  }, []);

  return (
    <li className={`header__left-item `} onClick={handleLeftMenuItemClick} id={props.id}>
      <div className="header__left-item-img" style={styleImg} />
      <div className="header__left-text">{props.title}</div>
    </li>
  );
}

export default HeaderLeftMenuItem;
