import React from "react";
import "./Footer.css";
import "./FooterMedia.css";

import { AppContext } from "../../contexts/AppContext";

function Footer() {
  // contexts
  const footerItems = React.useContext(AppContext).footer;

  return (
    <footer className="footer">
      <div className="line"></div>
      <div className="footer__container container">
        <div className="footer__row-1">
          <div className="footer__column">
            <ul className="footer__nav-1">
              <li className="footer__nav-item">
                <a href="#" className="footer__nav-link text text_effect_hover-underline-black">
                  {footerItems.nav1.link1}
                </a>
              </li>
              <li className="footer__nav-item">
                <a href="#" className="footer__nav-link text text_effect_hover-underline-black">
                  {footerItems.nav1.link2}
                </a>
              </li>
              <li className="footer__nav-item">
                <a href="#" className="footer__nav-link text text_effect_hover-underline-black">
                  {footerItems.nav1.link3}
                </a>
              </li>
              <li className="footer__nav-item">
                <a href="#" className="footer__nav-link text text_effect_hover-underline-black">
                  {footerItems.nav1.link4}
                </a>
              </li>
              <li className="footer__nav-item">
                <a href="#" className="footer__nav-link text text_effect_hover-underline-black">
                  {footerItems.nav1.link5}
                </a>
              </li>
              <li className="footer__nav-item">
                <a href="#" className="footer__nav-link">
                  {footerItems.nav1.link6}
                </a>
              </li>
            </ul>
          </div>
          <div className="footer__column">
            <ul className="footer__nav-2">
              <li className="footer__nav-item">
                <a href="#" className="footer__nav-link text text_effect_hover-underline-black">
                  {footerItems.nav2.link1}
                </a>
              </li>
              <li className="footer__nav-item">
                <a href="#" className="footer__nav-link text text_effect_hover-underline-black">
                  {footerItems.nav2.link2}
                </a>
              </li>
              <li className="footer__nav-item">
                <a href="#" className="footer__nav-link text text_effect_hover-underline-black">
                  {footerItems.nav2.link3}
                </a>
              </li>
              <li className="footer__nav-item">
                <a href="#" className="footer__nav-link text text_effect_hover-underline-black">
                  {footerItems.nav2.link4}
                </a>
              </li>
              <li className="footer__nav-item">
                <a href="#" className="footer__nav-link text text_effect_hover-underline-black">
                  {footerItems.nav2.link5}
                </a>
              </li>
              <li className="footer__nav-item">
                <a href="#" className="footer__nav-link text text_effect_hover-underline-black">
                  {footerItems.nav2.link6}
                </a>
              </li>
              <li className="footer__nav-item">
                <a href="#" className="footer__nav-link text text_effect_hover-underline-black">
                  {footerItems.nav2.link7}
                </a>
              </li>
              <li className="footer__nav-item">
                <a href="#" className="footer__nav-link text text_effect_hover-underline-black">
                  {footerItems.nav2.link8}
                </a>
              </li>
              <li className="footer__nav-item">
                <a href="#" className="footer__nav-link text text_effect_hover-underline-black">
                  {footerItems.nav2.link9}
                </a>
              </li>
            </ul>
          </div>
          <div className="footer__column">
            <h3 className="footer__title">{footerItems.col3.title}</h3>
            <p className="footer__text">{footerItems.col3.adress}</p>
            <p className="footer__text">
              Email: <b>mail@alta-profi.ru</b>{" "}
            </p>
            <ul className="footer__logo-row">
              <li>
                <a href="#" className="footer__logo-row-item footer__logo-row-item-1"></a>
              </li>
              <li>
                <a href="#" className="footer__logo-row-item footer__logo-row-item-2"></a>
              </li>
              <li>
                <a href="#" className="footer__logo-row-item footer__logo-row-item-3"></a>
              </li>
              <li>
                <a href="#" className="footer__logo-row-item footer__logo-row-item-4"></a>
              </li>
              <li>
                <a href="#" className="footer__logo-row-item footer__logo-row-item-5"></a>
              </li>
            </ul>
            <h3 className="footer__title">{footerItems.col3.apps}</h3>
            <div className="footer__app-row">
              <a href="#" className="footer__app-row-item footer__app-row-row-item-1"></a>
              <a href="#" className="footer__app-row-item footer__app-row-row-item-2"></a>
            </div>
          </div>
          <div className="footer__column">
            <h3 className="footer__title">{footerItems.form.title}</h3>
            <form action="#" name="footerForm" className="footer__form" novalidate>
              <input
                type="text"
                name="footerInputName"
                placeholder={footerItems.form.placeholder1}
                className="footer__form-item"
                id="footer-form-name"
                required
                minlength="2"
                maxlength="40"
              />
              <span id="footer-form-name-error" className="footer__form-text-error"></span>

              <input
                type="email"
                name="footerInputEmail"
                placeholder={footerItems.form.placeholder2}
                className="footer__form-item"
                id="footer-form-email"
                required
              />
              <span id="footer-form-email-error" className="footer__form-text-error"></span>

              <input
                type="tel"
                name="footerInputTel"
                placeholder={footerItems.form.placeholder3}
                className="footer__form-item"
                id="footer-form-tel"
                required
              />
              <span id="footer-form-tel-error" className="footer__form-text-error"></span>

              <textarea
                name="footerInputMessage"
                placeholder="Ваш текст"
                className="footer__form-item"
                id="footer-form-msg"
                required
              ></textarea>
              <span id="footer-form-msg-error" className="footer__form-text-error"></span>

              <span className="footer__form-text">{footerItems.form.confirm}</span>
              <span className="footer__form-link">
                {" "}
                <a href="#">{footerItems.form.agreement}</a>
              </span>

              <button type="submit" className="footer__button">
                {footerItems.form.send}
              </button>
            </form>
          </div>
        </div>
        <div className="footer__row-2">
          <span className="footer__row-2-text">{footerItems.row2.span1}</span>
          <a className="footer__row-2-text" href="#">
            {footerItems.row2.span2}
          </a>
        </div>
      </div>
    </footer>
  );
}

export default Footer;
