import React from "react";
import "./Map.css";
import "./MapMedia.css";

import { AppContext } from "../../contexts/AppContext";

// images
// import imgBefore from '../../images/intro-before.png';

function Map() {
  // contexts
  const mapItems = React.useContext(AppContext).map;
  const [isMapListOpen, setIsMapListOpen] = React.useState(false);
  function handleBtnClick() {
    setIsMapListOpen(true);
  }

  return (
    <section className="map">
      <div className="blue-area blue-area_left map__blue-area"></div>
      <div className="map__container container">
        <h2 className="map__title text__title">{mapItems.title}</h2>
        <div className="map__google-map">
          <iframe
            className="map__iframe"
            src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d2245.818182637536!2d37.59796331604614!3d55.7442880001159!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x46b54bac455322ab%3A0x17941c2652b2b1b0!2z0JzQtdGC0YDQviDQmtGA0L7Qv9C-0YLQutC40L3RgdC60LDRjw!5e0!3m2!1sru!2sru!4v1601004959060!5m2!1sru!2sru"
            title="map"
            // frameborder="0"
            // style="border:0;"
            // allowfullscreen=""
            // aria-hidden="false"
            // tabindex="0"
          ></iframe>
          <div className={isMapListOpen ? "map__list map__list_active" : "map__list"}>
            <div className="map__list-item">
              <h3 className="map__list-title">{mapItems.link1}</h3>
              <p className="map__list-text">{mapItems.diler}</p>
              <a href="#" className="map__list-open">
                <span className="arrow arrow_douwn"></span>
                <p>{mapItems.open}</p>
              </a>
              <a href="#" className="map__list-map">
                {mapItems.watch}
              </a>
            </div>
            <div className="map__list-item">
              <h3 className="map__list-title">{mapItems.link1}</h3>
              <p className="map__list-text">{mapItems.diler}</p>
              <a href="#" className="map__list-open">
                <span className="arrow arrow_douwn"></span>
                <p>{mapItems.open}</p>
              </a>
              <a href="#" className="map__list-map">
                {mapItems.watch}
              </a>
            </div>
          </div>
          <div className="map__menu-btn" onClick={handleBtnClick}>
            <span className="arrow arrow_douwn"></span>
            <p>{mapItems.button}</p>
          </div>
        </div>
      </div>
    </section>
  );
}

export default Map;
